body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #0a0533;
  color: #fff;
}

header {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 50px 0px 50px 0px;
}
.navbar{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    background-color: #16213e;
    height: 80px;
    align-items: center;
}
.logo{
    height: 4rem;
}
nav ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  gap: 40px;
}

nav li {
  cursor: pointer;
}
nav li:hover {
    text-decoration: underline;
  }

.intro {
  margin-top: 20px;
  width: 70%;
}

.intro h1 {
  font-size: 2.5rem;
}

.intro p {
  margin: 10px 0;
  /* text-align: justify; */
  padding-bottom: 20px;
}

button {
  background-color: #e94560;
  border: none;
  padding: 10px 20px;
  color: white;
  cursor: pointer;
  font-weight: bold;
}
button:hover {
    background-color: #9e283b;
  }

.skillset {
  padding: 50px 20px;
}

.skillset h2 {
  text-align: center;
  text-decoration: underline;
  margin-bottom: 30px;
}

.skills {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.skill {
  background-color: #16213e;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.skill:hover{
background-color: rgba(255, 255, 255, 0.678);
color:#16213e;
}

.contact {
  padding: 50px 20px;
  text-align: center;
  text-decoration: underline;
}

.contact form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}

input, textarea {
  width: 80%;
  padding: 10px;
  border-radius: 5px;
  border: none;
}

textarea {
  height: 100px;
}

.contact button {
  background-color: #e94560;
  border: none;
  padding: 10px 20px;
  color: white;
  cursor: pointer;
}
.contact button:hover {
    background-color: #9e283b;
  }
.footer{
    background-color: #16213e;   
    height: 55px;
}
.footer>p{
    text-align:center;
    padding-top: 20px;
}